/* Стили для контейнера теста */
.test-container {
    margin-top: 20px;
    padding: 20px;
    border: 1px solid #ccc;
    border-radius: 5px;
  }
  
  /* Стили для вопроса теста */
  .test-question {
    font-weight: bold;
    margin-bottom: 10px;
  }
  
  /* Стили для опций теста */
  .test-options {
    margin-bottom: 10px;
  }
  
  /* Стили для кнопки ответа */
  .test-submit-button {
    padding: 8px 16px;
    background-color: #007bff;
    color: #fff;
    border: none;
    border-radius: 5px;
    cursor: pointer;
  }
  
  /* Стили для кнопки ответа при наведении */
  .test-submit-button:hover {
    background-color: #0056b3;
  }
/* Стили для корневого элемента страницы */
.page-container {
    max-width: 1000px; /* Ширина страницы в пикселях */
    margin: 0 auto; /* Центрирование страницы по горизонтали */
    padding: 50px; /* Внутренние отступы страницы */
  }
  